.container {
    //background-color: #8BC6EC;
    //background-image: linear-gradient(135deg, #8BC6EC 0%, #9599E2 100%);
    background-image: linear-gradient(43deg, rgb(65, 88, 208) 0%, rgb(200, 80, 192) 46%, rgb(255, 204, 112) 100%);
    :global {
        .ant-table-wrapper {
            //background-color: #282c34 !important;
            box-shadow: rgba(0, 0, 0, 0.25) 0px 54px 55px, rgba(0, 0, 0, 0.12) 0px -12px 30px, rgba(0, 0, 0, 0.12) 0px 4px 6px, rgba(0, 0, 0, 0.17) 0px 12px 13px, rgba(0, 0, 0, 0.09) 0px -3px 5px;
            .ant-table-thead {
                background-color: #e78b8b;
            }
            .ant-table-thead > tr > th {
                background-color: transparent !important;
            }
            .ant-table-tbody {
                background-color: white;
                background-image: linear-gradient(160deg, #fff 0%, rgb(128, 208, 199) 100%);
            }
        }
    }
}
.title {
    text-align: center;
    h1 {
        color: #d92e2e;
    }
}
.position_fixed {
    position: fixed;
    top: 5%;
    z-index: 10;
}
.display {
    display: flex;
    width: 100%;
    flex-direction: row;
    justify-content: space-between;
}
.wrapper_search_all {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    background-color: #f1f1f1;
    border-radius: 10px;
    padding: 5px;
    .text {
        color: #ff0000;
        font-weight: bold;
        width: 100px;
    }
    :global {
        .ant-input-number-input {
            text-align: center;
        }
        .ant-input-number-handler-wrap {
            display: none;
        }
        span {
            display: none;
        }
    }
}
.display_flex_end {
    display: flex;
    width: 100%;
    flex-direction: row;
    justify-content: center;
    align-items: center;
}
.display_center {
    display: flex;
    width: 100%;
    flex-direction: row;
    justify-content: center;
    align-items: center;
}
.wrapper_search {
    background-color: #fbfbfb;
    box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
    justify-content: center;
    align-items: center;
    div {
        background-color: transparent;
    }
    :global {
        .ant-input {
            margin-left: 5px !important;
        }
    }
}
.box_shadow {
    box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
}

.wrapper_add_one {
    span {
        font-weight: bold;
        font-size: 15px;
    }
    :global {
        .row {
            margin-top: 5px;
        }
    }

}
